
import CardPanel from '@/components/card-panel.vue'
import { getCurrentInstance, onMounted, reactive } from 'vue'

type CardType = {
  title: string,
  number: number,
  iconColor: string,
  iconName: string
}

const getCards = () => {
  return reactive<Array<CardType>>([{
    title: '拜访人数',
    number: 100,
    iconColor: '#40c9c6',
    iconName: 'icon-person'
  }, {
    title: '组件数量',
    number: 500,
    iconColor: '#36a3f7',
    iconName: 'icon-component'
  }, {
    title: 'Star数量',
    number: 1,
    iconColor: '#f4516c',
    iconName: 'icon-star'
  }, {
    title: '留言数量',
    number: 30,
    iconColor: '#34bfa3',
    iconName: 'icon-message'
  }])
}

export default {
  components: {
    CardPanel
  },
  setup() {
    const cards = getCards()
    const { proxy }: any = getCurrentInstance()
    
    onMounted(() => {
      const lineChart = proxy.$echarts.init(document.querySelector('.line-charts'))
      lineChart.setOption({
        color: ['rgb(246, 81, 112)', 'rgb(0, 138, 205)'],
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['期望数量', '实际数量'],
          top: 15
        },
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: 'rgb(0, 138, 205)'
            }
          }
        },
        yAxis: {
          type: 'value',
          axisLine: {
            lineStyle: {
              color: 'rgb(0, 138, 205)'
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: '#eee'
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: ['rgb(249, 249, 249)', '#fff']
            }
          }
        },
        grid: {
          left: 80,
          top: 50,
          right: 50,
          bottom: 50,
        },
        series: [{
          name: '期望数量',
          data: [820, 932, 901, 934, 1290, 1330, 1320],
          type: 'line',
          smooth: true
        }, {
          name: '实际数量',
          data: [520, 332, 601, 134, 1190, 1390, 1220],
          type: 'line',
          smooth: true
        }]
      })
    })

    return {
      cards
    }
  }
}
